import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";
export function createApiSlice(baseUrl) {
    return createApi({
        reducerPath: "api",
        baseQuery: fetchBaseQuery({ baseUrl }),
        endpoints: (builder) => ({
            hello: builder.query({
                query: () => "hello",
            }),
            getTodos: builder.query({
                // IMPORTANT: keep it relative to baseUrl
                query: () => "todos",
            }),
        }),
    });
}
